.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (min-width: 414px) {
  .container {
    width: 414px;
  }
}

@media (min-width: 1024px) {
  .container {
    width: 1024px;
    padding-left: 47px;
    padding-right: 47px;
  }
}

@media (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 85px;
    padding-right: 85px;
  }
}

.button {
  color: #f3c653;
  letter-spacing: .02em;
  background-color: rgba(0, 0, 0, .4);
  border: 2px solid #fff;
  border-radius: 25px;
  padding: 14px 35px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.button:hover, .button:focus {
  background-color: #fff;
  border-color: #f3c653;
}

.media-list {
  flex-direction: row;
  gap: 9px;
  padding-bottom: 20px;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.media-list__icon {
  fill: rgba(243, 198, 83, .7);
}

.media-list__icon:hover, .media-list__icon:focus {
  fill: #fff;
}

.address__control {
  flex-direction: row-reverse;
  padding-right: 15px;
  display: flex;
}

.address__element {
  width: 210px;
  border: 1px solid rgba(243, 198, 83, .7);
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 8px 0 8px 6px;
  display: inline-flex;
}

.address__link {
  letter-spacing: .01em;
  color: #e5e5e5;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  line-height: 1.25;
}

.address__icon {
  fill: #f3c653;
}

.hero {
  max-width: 1280px;
  background-image: url("hero-home-mobile.9649fcc3.jpg");
  background-position: center;
  background-size: cover;
  margin: auto;
  padding-top: 64px;
}

@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
  .hero {
    background-image: url("hero-home-mobile@2x.7687ecfb.jpg");
  }
}

.hero__title {
  letter-spacing: .3em;
  color: #fff;
  text-align: center;
  margin-top: 70px;
  font-family: Film P3;
  font-size: 96px;
  line-height: 1.07;
}

.hero__subtitle {
  letter-spacing: .02em;
  color: #fff;
  text-align: center;
  margin-top: 32px;
  font-family: Montserrat;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.2;
}

.hero__motto {
  letter-spacing: .05em;
  color: #fdfbec;
  text-align: center;
  margin-top: 44px;
  font-family: Times New Roman;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.15;
}

.hero__control-buttons {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  margin-top: 113px;
  display: flex;
}

.hero__media {
  margin-top: 58px;
  padding-left: 15px;
}

.hero-rest {
  background-image: url("hero-rest-mobile.ff95c667.jpg");
}

@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
  .hero-rest {
    background-image: url("hero-rest-mobile@2x.449f2aa7.jpg");
  }
}

.hero-rest__motto {
  margin-top: 44px;
}

.hero-rest__control-buttons {
  margin-top: 113px;
}

.hero-hotel {
  background-image: url("hero-room-mobile.0e7916dd.jpg");
}

@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
  .hero-hotel {
    background-image: url("hero-room-mobile@2x.5104f3c2.jpg");
  }
}

.hero-hotel__control-buttons {
  margin-top: 178px;
}

.hero-room {
  background-image: url("hero-room-mobile.0e7916dd.jpg");
